import React, { useEffect, useState } from 'react';
import ScrollAnimation from 'react-animate-on-scroll';

import { isMobile } from '../Tools';

function Project(props) {
    const [clicked, handleClick] = useState(false);
    const [hovered, handleChangeHover] = useState(false);

    let height = window.screen.height/3.5 + 'px';

    let styles = {
        card: {
            display: 'inline-block',
            backgroundColor: props.color2,
            color: props.color1,
            borderStyle: 'solid',
            borderWidth: '5px',
            borderColor: props.color1,
            flex: 1,
            minWidth: isMobile() ? '85%' : '45%',
            height: height,
            margin: isMobile() ? '10px' : '10px',
            cursor: 'pointer'
        },
        clickedCard: {
            display: 'inline-block',
            backgroundColor: props.color1,
            color: props.color2,
            borderStyle: 'solid',
            borderWidth: '5px',
            borderColor: props.color1,
            flex: 1,
            minWidth: isMobile() ? '85%' : '45%',
            height: height,
            margin: isMobile() ? '10px' : '10px',
            cursor: 'pointer'
        },
        dummy: {
            display: 'inline-block',
            backgroundColor: props.color1,
            color: props.color1,
            borderWidth: '5px',
            borderColor: props.color2,
            flex: 1,
            minWidth: isMobile() ? '85%' : '45%',
            height: height,
            margin: isMobile() ? '10px' : '10px',
        },
        title: {
            position: 'absolute',
            textAlign: 'center',
            verticalAlign: 'middle',
            lineHeight: height,
            fontSize: '40pt',
            display: hovered ? 'none' : '',
            margin: '0 0 0 20px'
        }, 
        img: {
            display: 'block',
            height: '100%',
            width: '100%',
            margin: 'auto auto',
            opacity: hovered ? 1 : .05
        },
        text: {
            fontSize: isMobile() ? '10pt' : '12pt', 
            padding: isMobile() ? '5px' : '20px',
        }
    }

    if(props.dummy) {
        return <div style={styles.dummy}/>
    }

    if(clicked) {
        return(
            <ScrollAnimation animateIn="fadeIn" style={styles.clickedCard}>
                <div style={{width: '100%', height: '100%'}} onClick={() => handleClick(!clicked)}>
                    <p style={styles.text}>{props.project.description}</p>
                </div>
            </ScrollAnimation>
        )
    }
    
    let img = require(`../imgs/${props.project.name}.png`);

    return(
            <div animateIn='fadeInUp' style={styles.card}>
                <div style={{width: '100%', height: '100%', position: 'relative'}} 
                    onClick={() => handleClick(!clicked)} 
                    onMouseEnter={() => handleChangeHover(true)} 
                    onMouseLeave={() => handleChangeHover(false)}
                >
                    <div style={styles.title}>{props.project.name}</div>
                    <img src={img} style={styles.img}/>
                </div>   
            </div>
    )
}

export default Project;